/* eslint-disable no-multi-str */
import { styled } from 'styled-components';
import './App.css';
import './common.css';
import { Component } from 'react';

class DescriptionBoard extends Component{
    descriptions = {
      en:
      [{
        title: 'Opening of the shop in balcony !', 
        date: '1 pm -3pm 31st May', 
        description: 'With each box of fresh fish straight out of the fryer that the audience buys, \
          they will contribute to the quite literal deconstruction of the balcony. \
          In doing so, the installation challenges audiences to more actively participate \
          in the deconstruction and redistribution of the wealth and power held by the British monarchy. \
          \
          - I will sell or distribute the fish & chips boxes that I will have previously made. \
          Each will contain images of the royal family cut out from newspapers in a fish shape. \
          \
          The menu will be comprised of … \
          \
          By this, I attempt to invite royals to our daily table, change the way it is presented in a secular fish & chips shop.'
      },{
          title: 'My Royal Wave',
          description: 'One of the participatory performances will focus on the Royal Wave. \
          With each box that is "sold," audience members will be dismissed with a curt royal wave, \
          just as the monarch addresses the crowd from the Buckingham Palace balcony. \
          Audiences will then be invited to ascend the balcony to conduct their own royal waves, \
          and share in the spectacle that is typically only reserved for a select few. \
          Live cameras will capture a blown-up live-stream of these waves from the balcony streamed to a large monitor, \
          commenting upon the exaggerated nature of the British public\'s consumption of even the most inane gestures made by the monarchy.\
          '
      },{
          title: 'performance',
          description: 'Audience members will be invited to contribute further with personal items. \
          Initially, I will place a selection of items on the balcony, \
          ranging from: hats, litter, an actual serving of fish and chips, and even bikinis. \
          Such items are those that would be considered taboo were they ever to be found on the real royal balcony. \
          They will serve to disrupt the \'norm\' created by royal traditions and ceremonies. \
          Ultimately, through this installation, I intend to create an intermediary space between the public and the private. \
          The items placed on the balcony will interrupt public space on behalf of the private. \
          The public will, likewise, be encouraged during the exhibition term to replace my transgressive items with any of their own choosing. \
          In so doing, they will be free to give the place their own meaning, express their imagination, \
          and participate in the balcony\'s politics - previously only accessible to the monarchy. \
          The public will be free to take photos and selfies which they can upload to social media or share with friends. \
          -this will go over during the show '
      }]
    ,
    kr:
    [{
      title: '발코니에서의 오프닝!', 
        date: '1 pm -3pm 31st May', 
        description: '관람객이 막 튀김기에서 건져올린 생선 하나를 사갈 때마다, \
          관람객들은 말 그대로 발코니를 무너뜨리는데 기여하게 됩니다. \
          설치 작품은 관람객들로서 하여금 적극적으로 \
          영국 군주국의 부와 권력을 무너뜨리고 재분배하는 과정에 참여하는 것을 유도합니다. \
          \
          \n- 저는 기존에 직접 만든 피쉬&칩스 포장 상자를 팔거나 나누어드릴 예정입니다. \
          각 포장에는 신문지에서 물고기 모양으로 잘라낸 왕가의 사진이 들어있습니다. \
          \
          The menu will be comprised of … \
          \
          By this, I attempt to invite royals to our daily table, change the way it is presented in a secular fish & chips shop.'
    },{
      title: 'My Royal Wave',
      description: 'One of the participatory performances will focus on the Royal Wave. \
      With each box that is "sold," audience members will be dismissed with a curt royal wave, \
      just as the monarch addresses the crowd from the Buckingham Palace balcony. \
      Audiences will then be invited to ascend the balcony to conduct their own royal waves, \
      and share in the spectacle that is typically only reserved for a select few. \
      Live cameras will capture a blown-up live-stream of these waves from the balcony streamed to a large monitor, \
      commenting upon the exaggerated nature of the British public\'s consumption of even the most inane gestures made by the monarchy.\
      '
    },{
      title: '퍼포맨스',
      description: '관람객은 본인 소지품을 작품에 기여할 수 있습니다. \
      제일 먼저, 제가 여러 물건들을 발코니에 올려둡니다. \
      모자나 쓰레기로 시작해서 실제 피쉬 앤 칩스, 심지어는 비키니까지 될 수 있습니다. \
      이런 물건들은 현실의 로열 발코니에서는 찾아볼 수 없습니다. \
      They will serve to disrupt the \'norm\' created by royal traditions and ceremonies. \
      Ultimately, through this installation, I intend to create an intermediary space between the public and the private. \
      The items placed on the balcony will interrupt public space on behalf of the private. \
      The public will, likewise, be encouraged during the exhibition term to replace my transgressive items with any of their own choosing. \
      In so doing, they will be free to give the place their own meaning, express their imagination, \
      and participate in the balcony\'s politics - previously only accessible to the monarchy. \
      The public will be free to take photos and selfies which they can upload to social media or share with friends. \
      \n-전시 내내 진행합니다.'
  }]};

  render(props){
      return <Container id="Sessions">
        <Category>
        {this.props.language === 'en' ? 'Sessions' : '세션'}
        </Category>
        <Content>
          {this.descriptions[this.props.language].map((section, ind) => 
              <SessionDisplay key={ind.toString()}><Title>{section['title']}</Title><br /><Description>{section['description']}</Description></SessionDisplay>)
          }
        </Content>
      </Container>;

  }
}

const Container = styled.div`
  scroll-margin-top: var(--nav-height);
`;

const Category = styled.div`
  line-height: var(--category-line-height);
  font-size: var(--category-font);
  font-weight: bold;
`;

const Content = styled.div`

`;

const SessionDisplay = styled.div`
  min-height: var(--min-height);
  background-color: white;
  border-radius: var(--display-border-radius);
  border-color: black;
  padding: var(--display-padding);
  text-align: justify;
`;

const Title = styled.div`
  font-size: var(--title-font);
  text-align: left; 
  font-weight: bold;
`;

const Description = styled.code`
  font-size: var(--description-font);
  line-height: 1.2;
  text-align: justify;
  
`;
export default DescriptionBoard;
